@use  "variables" as v;
html{
    font-size: 62.5%;
    box-sizing: border-box;

}
*,
*:before,
*:after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;

    // cuando este presente esa clase ejecutamos el siguiente codigo
    &.fijar-body{
        overflow: hidden; // para no dar scroll
    }
    // esto es para cuando la barra se muestre abajo no se mire de golpe
    &.body-scroll{
        padding-top: 8.6rem;
    }
}

p{
    font-size: 2rem;
    color: v.$negro;
    line-height: 2.5rem;
}

.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

h1, h2, h3{
    margin: 0 0 3rem 0;
}

h1{
    font-size: 4rem;
}

h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}

img{
    max-width: 100%;
    display: block;
}