@use 'base/variables' as v;
@use 'base/mixins' as m;




.header{
        background-color: v.$verde;  
        @include m.tablet{
            &.fijo{
                left: 0;
                top: 0;
                // cuando tenga esta clase va a tener una posicion fija
                // esta clase la a単adimos dsd galeria.js
                position: fixed;
                width: 100%;
                box-shadow: .1rem .2rem .3rem #333;
            }
        }
        
      
        h1{
    color:v.$blanco;
    text-align: center;

        @include m.tablet{
            margin: 0;
        }
    }
        .contenido-header{
            padding: 1.5rem;
            @include m.tablet{
            display: flex; // regresando el h1 y el a su orden incial
            justify-content: space-between;
            align-items: center; // para centrar los enlaces 
             }
            }
}

.navegacion-principal{
    @include m.tablet{
        display: flex;
        gap: 2rem;
    }
    a{
        display: block; // para colocar los enlaces de arriba hacia abajo
        color:v.$blanco;
        font-size: 2.4rem;
        text-align: center;
        margin-bottom: 1rem; // para darle espacio a los enlaces cuando en 480px
        @include m.tablet{
           margin: 0; // para quitar el margen y que no nos afecte cuando este mostrado en una pantalla mas grande
    }

        &:hover{
            color:v.$amarillo;
        }
    }
}
